import { useState, useEffect } from "react";
import {FetchResize} from '../assets/FunctionStorage'
function GetMDData(source) {
    const[dimensions, setDimensions]=useState(window.innerWidth)
    let fet = FetchResize()
    if (fet != dimensions){
        setDimensions(fet)
    }
    
    const [content, setContent] = useState();
    useEffect(() => {
        fetch(source)
            .then((res) => res.text())
            .then((md) => {
                setContent(md)
            })
    }, [source])
    return content
}

export default GetMDData