const RadioInput = ({label, value, checked, setter}) => {
	return (
		<li>
	  <label>
	    <input 
			 	id='check'
				type="radio" 
				checked={checked == value}
	      onChange={() => setter(value)} 
			/>
	    <span>{label}</span>
	  </label>
		</li>
	);
};

export default RadioInput