import { useState } from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import {markmap, ShowMarkMap, GUIViewer, GetMDData} from './assets/FunctionStorage';
function Root() {
    const [checked, setChecked] = useState(0)
    let markDown = GetMDData(markmap[checked].fileSource)
    return (
        <div> 
          <GUIViewer checked={checked} onChange={setChecked} />
          <ShowMarkMap markDown={markDown} option={{autoFit: true}}/>
        </div>
    );
}

ReactDOM.render(<Root/>, document.getElementById('root'));